.parallax4 { 
    /* The image used */
    background-image: url("./bg3.jpg");

    /* Set a specific height */
    height: auto; 

    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.contact-link {
  color: #fff;
}

.gif-2 {
  width: 100%;
  max-width: 100px;
}


