.parallax5 { 
    /* The image used */
    background: -webkit-gradient(
        linear, 
        left top, left bottom, 
        from(rgba(0, 0, 0, 0.4)), 
        to(rgba(0, 0, 0, 0.4))), url("./bg5.jpg");
    background: -webkit-linear-gradient(
        rgba(0, 0, 0, 0.4), 
        rgba(0, 0, 0, 0.4)), url("./bg5.jpg");
    background: -o-linear-gradient(
        rgba(0, 0, 0, 0.4), 
        rgba(0, 0, 0, 0.4)), url("./bg5.jpg");
    background: linear-gradient(
        rgba(0, 0, 0, 0.4), 
        rgba(0, 0, 0, 0.4)), url("./bg5.jpg");

    /* Set a specific height */
    height: auto; 

    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    margin-top: -1rem;
}

ul.timeline {
    list-style-type: none;
    position: relative;
}
ul.timeline:before {
    content: ' ';
    background: #d4d9df;
    display: inline-block;
    position: absolute;
    left: 29px;
    width: 2px;
    height: 100%;
    z-index: 400;
}
ul.timeline > li {
    margin: 20px 0;
    padding-left: 20px;
    padding-bottom: 5%;
    padding-top: 5%;
    text-align: left;
}
ul.timeline > li:before {
    content: ' ';
    background: white;
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    border: 2px solid #EE1D23;
    left: 25px;
    margin-top: 2%;
    width: 10px;
    height: 10px;
    z-index: 400;
    text-align: left;
}





